import { validSlugs } from "@/utils/slugs/projectSlugs";

export default function ProjectDynamicPage({ params }) {
  const { projectId } = params;
  // No slug in params — derive slug from pathname
  const pathname = usePathname();
  const parts = pathname.split("/").filter(Boolean);
  const slug = parts.slice(2); // after /project/[id]/

  // Dummy Data
  const tasks = [
    { id: 1, title: "Task One", status: "Open" },
    { id: 2, title: "Task Two", status: "In Progress" },
  ];

  const bugs = [
    { id: 101, title: "Login Bug", severity: "High" },
    { id: 102, title: "UI Glitch", severity: "Low" },
  ];

  // -----------------------------------
  // Handle NO SLUG → treat as 'overview'
  // -----------------------------------
  if (slug.length === 0) {
    const currentModule = "overview";
    return (
      <div>
        <h1>Project Overview - {projectId}</h1>
        <p>This is dummy project overview.</p>
      </div>
    );
  }

  const currentModule = slug[0];

  // -----------------------------------
  // /project/111/task → Task List
  // -----------------------------------
  if (currentModule === "task" && slug.length === 1) {
    return (
      <div>
        <h1>Task List</h1>
        <ul>
          {tasks.map((task) => (
            <li key={task.id}>{task.title} - {task.status}</li>
          ))}
        </ul>
      </div>
    );
  }

  // -----------------------------------
  // /project/111/bug → Bug List
  // -----------------------------------
  if (currentModule === "bug" && slug.length === 1) {
    return (
      <div>
        <h1>Bug List</h1>
        <ul>
          {bugs.map((bug) => (
            <li key={bug.id}>{bug.title} - {bug.severity}</li>
          ))}
        </ul>
      </div>
    );
  }

  return <div>404 Not Found</div>;
}

